import React, { PureComponent } from "react";

class SnippetModal extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    const { item } = this.props;

    return (
      <p>
        <b>: </b>{" "}
        {item.vacancy.snippet.responsibility
          ? item.vacancy.snippet.responsibility
          : " "}
      </p>
    );
  }
}

export default SnippetModal;
